.global-controls .control-btn {
	width: 60px;
	height: 40px;
}

.global-volume {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	margin-top: 15px;
}

.global-volume .VolumeSlider.isHorizontal  {
	width: 100px;
	margin-top: 0;
}

.global-volume .fa-volume-off {
	margin-top: 0;
}

@media screen and (max-width:980px) {
	.global-controls {
		-ms-flex-order: 1;
		    order: 1;
		margin-bottom: 30px;
	}
}


