.title {
	font-size: 20px;
	margin: 70px 0 50px;
	color: #21cfdf;
	text-align: center;
}

.dropzone-area {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.dropzone {
	max-width: 450px;
	width: 100%;
}

.dropzone > div {
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	cursor: pointer;
}

.dropzone-info {
	color: #276a72;
	padding: 15px;
	text-align: center;
	-webkit-transition: color 0.4s ease;
	-o-transition: color 0.4s ease;
	transition: color 0.4s ease;
}

.dropzone-info:hover {
	color: #21cfdf;
}

.warning {
	color: #722727;
	margin-top: 40px;
	text-align: center;
}

.button {
	color: #fff;
	font-weight: 600;
	text-decoration: none;
	background: #276a72;
	width: 160px;
	display: block;
	text-align: center;
	padding: 12px 0;
	border-radius: 5px;
	-webkit-transition: background 0.4s linear;
	-o-transition: background 0.4s linear;
	transition: background 0.4s linear;
	margin: 30px auto;
}

.button:hover {
	background: #21cfdf;
}

@media screen and (max-width:960px) {
	.dropzone-area {
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: center;
		    align-items: center;
	}
}




