.plate {
	width: 250px;
	height: 250px;
	background: url("../../img/plate.png") no-repeat;
	-ms-flex-item-align: center;
	    align-self: center;
	margin-bottom: 30px;
	border-radius: 50%;
	-webkit-transition: -webkit-box-shadow 0.3s ease;
	transition: -webkit-box-shadow 0.3s ease;
	-o-transition: box-shadow 0.3s ease;
	transition: box-shadow 0.3s ease;
	transition: box-shadow 0.3s ease, -webkit-box-shadow 0.3s ease;
}

.plate.active {
	-webkit-box-shadow: 0px 0px 10px 2px #21cfdf;
	box-shadow: 0px 0px 10px 2px #21cfdf;
	-webkit-animation: plate-animation 2s linear infinite;
	        animation: plate-animation 2s linear infinite;
}

@-webkit-keyframes plate-animation {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to   { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes plate-animation {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to   { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


