.tracks {
	max-width: 450px;
	width: 100%;
	padding: 0;
	margin: 30px 0;
	-ms-flex-item-align: center;
	    align-self: center;
}

.tracks .track {
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	cursor: default;
}

.track:last-of-type {
	border-bottom: 1px solid #212226;
}

.tracks .track i {
	color: #722727;
	cursor: pointer;
	-webkit-transition: color 0.4s ease;
	-o-transition: color 0.4s ease;
	transition: color 0.4s ease;
}

.tracks .track i:hover {
	color: #df213b;
}

.track:hover, .track.active {
	color: #21cfdf;
}

.time {
	margin-left: auto;
}


