.welcome {
	height: calc(100vh - 63px);
	background: url("../../img/home bg.png") no-repeat center center;
	background-size: cover;
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.overlay {
	content: '';
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.8);
	position: absolute;
	z-index: 1;
}

.welcome-title {
	position: relative;
	margin: 0;
	z-index: 2;
	color: #21cfdf;
	text-align: center;
}

.welcome .button {
	position: relative;
	z-index: 2;
}





