.playlist {
	max-width: 450px;
	width: 100%;
	padding: 0;
	margin: 30px 0;
	-ms-flex-item-align: center;
	    align-self: center;
}

.track {
	display: -ms-flexbox;
	display: flex;
	color: #d3d2d0;
	list-style: none;
	font-size: 13px;
	padding: 12px;
	background: #2b2b2b;
	border: 1px solid #212226;
	border-bottom: none;
	border-radius: 2px;
	cursor: pointer;
	-webkit-transition: color 0.4s ease;
	-o-transition: color 0.4s ease;
	transition: color 0.4s ease;
}

.track:last-of-type {
	border-bottom: 1px solid #212226;
}

.track:hover, .track.active {
	color: #21cfdf;
}

.time {
	margin-left: auto;
}


